.testimonials{

    width: 80%;
    margin: auto;
    text-align: center;
    padding-top: 100px;
  
  }
  
  .testimonial-col{
    flex-basis:44%;
    border-radius: 10px;
    margin-bottom: 5%;
    text-align:left;
    background:#fff3f3;
    padding:25px;
    cursor: pointer;
    display:flex;
  }
  
  .testimonial-col img{
    height:40px;
    margin-left: 5px;
    margin-right: 30px;
    border-radius: 50%;
  }
  
  
  .testimonial-col p{
   padding: 0;
  }
  
  .testimonial-col h3{
    padding:0;
    text-align: left;
  }