.header {
    min-height: 100vh;
    width: 100%;
    background-image: linear-gradient(rgba(4,9,30,0.7),rgba(4,9,30,0.7)),url("../../src/Assets/banner.png");
    background-position: center;
    background-size: cover;
    position: relative;
  }
  
  nav{
    display:flex;
    padding:2% 6%;
    justify-content:space-between;
    align-items:center;
  
  }
  
  nav img{
    width: 150px;
  }
  
  .nav-links{
    flex: 1;
    text-align:right;
  }
  
  .nav-links ul li {
    list-style: none;
    display: inline-block;
    padding:8px 12px;
    position: relative;
  }
  
  .nav-links ul li a{
    color: white;
    text-decoration: none;
    font-size: 13px;
  }
  
  .nav-links ul li::after{
    content:"";
    width: 0%;
    height:2px;
    background: #f44336;
    display:block;
    margin:auto;
  }
  
  .nav-links ul li:hover::after{
  
    width: 100%;
    transition:0.5s;
  } 
  
  .text-box{
    width:90%;
  
    color:white;
    position:absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }
  
  .text-box h1{
  
    font-size: 62px;
  }
  
  .text-box p{
    margin:10px 0 40px;
    font-size: 14px;
    color: white;
  }
  
  
  .hero-btn{
    display: inline-block;
    text-decoration: none;
    color: white;
    border: 1px solid white;
    padding: 12px 34px;
    background:transparent;
    position: relative;
    cursor: pointer;
  }
  
  
  .hero-btn:hover{
  
  border:1px solid #f44336;
  background:#f44336;
  transition:1s;
  }
  

  h2{
    color:white;
  }


  @media (max-width: 500px){

    .text-box h1{
  
      font-size: 30px;
    }

    .nav-links{
      flex: 1;
      text-align:right;
      display:none;
    }

  }