.facilities{

    width: 80%;
    margin: auto;
    text-align: center;
    padding-top: 50px;
  }
  
  .facilities-col{
    flex-basis: 31%;
    border-radius: 10px;
    margin-bottom: 5%;
    text-align:left;
  }
  
  .facilities-col img{
    width: 100%;
    border-radius: 10px;
  }
  
  .facilities-col p{
    padding:0;
  }
  
  .facilities-col p{
    margin-top: 16px;
    margin-bottom:15px;
    text-align: left;
  }


  @media (max-width: 600px){

    .row{
      display: flex;
      flex-direction: column;
    }


  }